import styles from "./ReactionButtons.module.scss";
import CheckSvg from "../../assets/check.svg?react";
import SendSvg from "../../assets/send.svg?react";
import { useState } from "react";
import { Button, Textarea, TextareaOnChangeData } from "@fluentui/react-components";

interface FeedbackProps {
    onComment: (comment: string) => void
}

export const Feedback = ({ onComment }: FeedbackProps) => {
    const onClick = () => {
        console.debug("about to trigger onComment prop with ", text)
        onComment(text)
    }
    const onChange = (ev: React.ChangeEvent<HTMLTextAreaElement>, data: TextareaOnChangeData) => {
        setText(data.value);
        setIsDisabled(!data.value);
    }
    const [isCommentVisible, setIsCommentVisible] = useState(false)
    const [isDisabled, setIsDisabled] = useState(true)
    const [text, setText] = useState("");
    return (
        <div className={styles.feedbackContainer}>
            <p>How was your experience with this chatbot?</p>
            <ExperienceList onSelect={(s) => setIsCommentVisible(!!s)} />

            {isCommentVisible && <>
                <p>Please explain (optional)</p > <div className={styles.comment}>
                    <div className={styles.textarea}>
                        <Textarea autoFocus value={text} onChange={onChange} />
                    </div>
                    <Button iconPosition="before" size="large" icon={<SendSvg />} disabled={isDisabled} onClick={onClick} />
                </div>
            </>}
        </div>
    )
}

interface ExperienceListProps {
    onSelect: (selected: string) => void;
}

const ExperienceList = ({ onSelect }: ExperienceListProps) => {
    const list = ['Very relevant', 'Accurate', 'Comprehensive', 'Clear', 'Other...'];
    const [checkedList, setCheckedList] = useState(new Array(list.length).fill(false));
    const onClick = (text: string) => {
        setCheckedList(checkedList.map((_, index) => list.indexOf(text) === index))
        onSelect(text)
    }
    return (
        <ul>
            {list.map((text, index) => (
                <ExperienceItem key={`${index}-${text}`} text={text} onItemClicked={onClick} isChecked={checkedList[index]} />
            ))}
        </ul>
    )
}

interface ExperienceItemProps {
    text: string;
    onItemClicked: (s: string) => void;
    isChecked: boolean;
}

const ExperienceItem = ({ text, isChecked, onItemClicked }: ExperienceItemProps) => {

    const onClick = () => {
        onItemClicked(text)
    }

    if (isChecked) {
        return (
            <li className={styles.checked}>
                <CheckSvg />
                {text}
            </li>
        )
    }

    return (
        <li>
            <button className={styles.button} onClick={onClick}>
                {text}
            </button>
        </li>
    )
}