import styles from "./ReactionButtons.module.scss";
import Like from "../../assets/like.svg?react";
import Dislike from "../../assets/dislike.svg?react";
import { useState } from "react";
import { Feedback } from "./Feedback";
import { Button } from "@fluentui/react-components";

export const ExperienceSharing = () => {
    const [isButtonChecked, setIsButtonChecked] = useState([false, false]);
    const onClick = (isChecked: boolean, isLike: boolean) => {

        setIsButtonChecked([isLike && isChecked, !isLike && isChecked]);
    }
    console.debug('isButtonChecked', isButtonChecked);
    return (
        <>
            <div className={styles.allContainer}>
                <ReactionButton isLike isChecked={isButtonChecked[0]} callback={onClick} />
                <ReactionButton isChecked={isButtonChecked[1]} callback={onClick} />
            </div>
            {(isButtonChecked[0] || isButtonChecked[1]) && <Feedback onComment={(c: string) => console.debug(`You've just comment ${c}`)} />}
        </>
    )
}

interface ReactionButtonProps {
    isLike?: boolean;
    callback: (isChecked: boolean, isLike: boolean) => void;
    isChecked: boolean;
}

const ReactionButton = ({ isLike, callback, isChecked }: ReactionButtonProps) => {
    const onClick = () => {
        callback(!isChecked, !!isLike)
    }

    return (
        <div className={`${styles.container} ${isChecked && styles.checked}`}>
            <Button iconPosition="before" size="large" icon={isLike ? <Like /> : <Dislike />} onClick={onClick} />
        </div>
    )
}

