import { useMsal } from "@azure/msal-react";
import { useState } from "react";
import styles from "./LoginButton.module.css";
import { getRedirectUri, loginRequest } from "../../authConfig";

interface LoginButtonProps {
    autoLogin?: boolean
}

export const LoginButton = ({ autoLogin }: LoginButtonProps) => {
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const { instance } = useMsal();
    const activeAccount = instance?.getActiveAccount();
    const [isTogglePressed, setIsTogglePressed] = useState(false);

    const handleLoginPopup = () => {
        /**
         * When using popup and silent APIs, we recommend setting the redirectUri to a blank page or a page
         * that does not implement MSAL. Keep in mind that all redirect routes must be registered with the application
         * For more information, please follow this link: https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-browser/docs/login-user.md#redirecturi-considerations
         */
        instance.loginPopup({
            ...loginRequest,
            redirectUri: getRedirectUri()
        })
            .then(() => setIsLoggedIn(true))
            .catch(error => console.log(error));
    };
    const handleLogoutPopup = () => {
        instance.logoutPopup({
            mainWindowRedirectUri: "/", // redirects the top level app after logout
            account: instance.getActiveAccount()
        })
            .then(() => setIsLoggedIn(false))
            .catch(error => console.log(error));
    };

    const handleToggle = () => {
        setIsTogglePressed(true);
    }

    if (isTogglePressed) {
        isLoggedIn && handleLogoutPopup();
    } else if (autoLogin) {
        !isLoggedIn && handleLoginPopup();
    }

    const logoutText = `${activeAccount?.name?.split(', ').map((s: string) => s[0]).join('')}`;
    console.debug('activeAccount', activeAccount)
    return (
        activeAccount ?
            <button
                className={styles.loginButton}
                onClick={handleToggle}
            >{logoutText}</button> :
            "Login"

    );
};
