import { Example } from "./Example";
import { RetrievalMode } from "../../api";

import styles from "./Example.module.css";

export type ExampleModel = {
    text: string;
    value: string;
};

const EXAMPLES: ExampleModel[] = [
    {
        text: "What is ADI?",
        value: "What is ADI?"
    },
    { text: "Please list ADI available speeds", value: "Please list ADI available speeds" },
    { text: "Why should I buy an ADI?", value: "Why should I buy an ADI?" }
];

const EXAMPLES_CODE: ExampleModel[] = [
    {
        text: "What was the company revenue in 2014?",
        value: "What was the company revenue in 2014?"
    },
    { text: "How many locations does the company have?", value: "How many locations does the company have?" },
    { text: "Walk me through the sales process", value: "Walk me through the sales process" }
];

interface Props {
    onExampleClicked: (value: string) => void;
    retrievalMode: RetrievalMode;
}

export const ExampleList = ({ onExampleClicked, retrievalMode }: Props) => {
    const examples = retrievalMode == RetrievalMode.Code ? EXAMPLES_CODE : EXAMPLES_CODE;
    return (
        <ul className={styles.examplesNavList}>
            {examples.map((x, i) => (
                <li key={i}>
                    <Example text={x.text} value={x.value} onClick={onExampleClicked} />
                </li>
            ))}
        </ul>
    );
};
