import { useMemo } from "react";
import { Stack } from "@fluentui/react";
import DOMPurify from "dompurify";

import styles from "./Answer.module.scss";

import { ChatAppResponse, RetrievalMode, getStorageFilePath } from "../../api";
import { parseAnswerToHtml } from "./AnswerParser";
import { AnswerMarkdown } from "./AnswerMarkdown";
import { ExperienceSharing } from "../ExperienceSharing"
import { Citiation } from "../Citiation";
import { useCitiationClickEvents } from "./hooks";

interface Props {
    answer: ChatAppResponse;
    isSelected?: boolean;
    isStreaming: boolean;
    retrievalMode: RetrievalMode;
    onCitationClicked: (filePath: string) => void;
    onThoughtProcessClicked: () => void;
    onSupportingContentClicked: () => void;
    onFollowupQuestionClicked?: (question: string) => void;
    showFollowupQuestions?: boolean;
}

export const Answer = ({
    answer,
    isSelected,
    isStreaming,
    retrievalMode,
    onCitationClicked,
    onFollowupQuestionClicked,
    showFollowupQuestions
}: Props) => {


    const followupQuestions: any[] = [];
    const messageContent = answer.choices[0].message.content;
    const citations = answer.choices[0].message.context.citations;
    const parsedAnswer = useMemo(() => parseAnswerToHtml(messageContent, isStreaming, onCitationClicked), [answer]);
    const sanitizedAnswerHtml = DOMPurify.sanitize(parsedAnswer.answerHtml);
    const docPaths = citations
        ? Object.fromEntries(
            citations.map((item: any, index: number) => [
                `doc${index + 1}`,
                { path: item.url || "", name: item.title || "", content: item.content || "" }
            ])
        )
        : {};

    useCitiationClickEvents({ docPaths, retrievalMode, onCitationClicked, getStorageFilePath });
    return (
        <Stack className={`${styles.answerContainer} ${isSelected && styles.selected}`} verticalAlign="space-between">
            <Stack.Item grow>
                <AnswerMarkdown sanitizedAnswerHtml={sanitizedAnswerHtml}></AnswerMarkdown>
            </Stack.Item>
            {!!parsedAnswer.citations.length && (
                <Stack.Item>
                    <Stack horizontal wrap tokens={{ childrenGap: 5 }}>
                        {parsedAnswer.citations.map((x, i) => {
                            const docObject = docPaths[`${x}`];
                            if (docObject) {
                                const { name, path } = docObject;
                                const fullPath = getStorageFilePath(path, retrievalMode);
                                const text = `${++i}. ${name}`;
                                return (
                                    <Citiation key={i + name} text={text} onClick={() => onCitationClicked(fullPath)} />
                                );
                            }
                        })}
                    </Stack>
                </Stack.Item>
            )}
            {!!followupQuestions?.length && showFollowupQuestions && onFollowupQuestionClicked && (
                <Stack.Item>
                    <Stack horizontal wrap className={`${!!parsedAnswer.citations.length ? styles.followupQuestionsList : ""}`} tokens={{ childrenGap: 6 }}>
                        <span className={styles.followupQuestionLearnMore}>Follow-up questions:</span>
                        {followupQuestions.map((x, i) => {
                            return (
                                <a key={i} className={styles.followupQuestion} title={x} onClick={() => onFollowupQuestionClicked(x)}>
                                    {`${x}`}
                                </a>
                            );
                        })}
                    </Stack>
                </Stack.Item>
            )}
            <Stack.Item>
                <ExperienceSharing />
            </Stack.Item>

        </Stack>
    );
};
