import rehypeMermaid from "rehype-mermaid";
import remarkParse from "remark-parse";
import remarkRehype from "remark-rehype";
import remarkGfm from "remark-gfm";
import { unified } from "unified";
import rehypeStringify from "rehype-stringify";
import rehypeRaw from "rehype-raw";

import styles from "./Answer.module.scss";
import { useEffect, useState } from "react";

interface Props {
    sanitizedAnswerHtml: string;
}

const enum MarkdownErrorTypes {
    Mermaid = "mermaid"
}

class MarkdownError extends Error {
    constructor(
        message: string,
        public error: any,
        public type: MarkdownErrorTypes
    ) {
        super(message);
        Object.setPrototypeOf(this, MarkdownError.prototype);
    }
}

const processor = unified()
    .use(remarkParse)
    .use(remarkRehype, { allowDangerousHtml: true })
    .use(rehypeRaw)
    .use(rehypeMermaid, {
        errorFallback(...args) {
            const [element, diagram, error, file] = args;
            console.log(error);
            throw new MarkdownError("mermaid error", error, MarkdownErrorTypes.Mermaid);
        }
    })
    .use(remarkGfm)
    .use(rehypeStringify);

export const AnswerMarkdown = ({ sanitizedAnswerHtml }: Props) => {
    const [finalAnswer, setAnswer] = useState<string>("");

    useEffect(() => {
        (async () => {
            try {
                const { value } = await processor.process(sanitizedAnswerHtml);
                setAnswer(value.toString());
            } catch (err: any) {
                if (err instanceof MarkdownError) {
                    const markdownError: MarkdownError = err;
                    if (markdownError.type == MarkdownErrorTypes.Mermaid) {
                        const markdown = `
${markdownError.error.message}

\`\`\`mermaid
graph TD;
    Please -->Retry;
    Retry-->Please;
\`\`\`
`;

                        const { value } = await processor.process(markdown);
                        setAnswer(value.toString());
                    }
                }
            }
        })();
    }, []);

    return <div className={styles.answerText} dangerouslySetInnerHTML={{ __html: finalAnswer }}></div>;
};
