import { useEffect } from "react";
import { RetrievalMode } from "../../api";

export interface CitiationClickEventsProps {
    getStorageFilePath: Function;
    docPaths: any;
    retrievalMode: RetrievalMode;
    onCitationClicked: Function;
}

export function useCitiationClickEvents({ docPaths, retrievalMode, onCitationClicked, getStorageFilePath }: CitiationClickEventsProps) {

    const onSupClick = (key: string) =>
        () => {
            if (docPaths[key]) {
                const { path } = docPaths[key];
                const fullPath = getStorageFilePath(path, retrievalMode);
                onCitationClicked(fullPath);
            }
        }

    useEffect(() => {
        console.debug("mount useCitiationClickEvents")
        setTimeout(() => {
            console.debug("scrolling to bottom of window")
            window.scrollTo(0, document.body.scrollHeight);
            console.debug("focus on question input")
            window.document.getElementById("question-input")?.focus();
            Object.keys(docPaths).forEach((key: string, index: number) => {
                const buttons = document.getElementsByTagName("button");
                for (const button of buttons) {
                    const part = button.getAttribute("data-part");
                    if (part === key) {
                        console.debug("Adding event listener for citation", key);
                        button.addEventListener("click", onSupClick(key));
                    }
                }
            });
        }, 50)
    }, []);

    useEffect(() => () => {
        console.debug("unmount useCitiationClickEvents")
        Object.keys(docPaths).forEach((key: string) => {
            console.debug("Removing event listener for citation", key);
            window.removeEventListener("click", onSupClick(key));
        });
    }, []);
}